import Vue from 'vue'
import Router from 'vue-router'
import Login from "../components/Login";
import Main from "../components/Main";
import FunctionCase from "../page/FunctionCase"
import FirstPage from "../util/FirstPage"
import APICase from  '../page/APICase'
import APITest from  '../page/APITest'
import DDLCheck from "../page/DDLCheck";

Vue.use(Router);


export default new Router({
  routes:[
    {
      //登录页
      path: '',
      name: 'Login',
      component: Login
    },
    {
      //首页
      path: '/main',
      name: 'Main',
      component: Main,
      children:[
        {
          path: '/FirstPage',
          name: 'FirstPage',
          component: FirstPage
        },
        {
          path: '/FunctionCase',
          name: 'FunctionCase',
          component: FunctionCase
        },
        {
          path: '/APICase',
          name: 'APICase',
          component: APICase
        },
        {
          path: '/APITest',
          name: 'APITest',
          component: APITest
        },
        {
          path: '/DDLCheck',
          name: 'DDLCheck',
          component: DDLCheck
        }
      ]
    }
  ]
})



