<template>
    <div style="height:100%; padding: 30px">
      <span>接口用例</span>
      <div>
        <el-form>
          <el-form-item>
            <el-button type="primary" @click="add">新增</el-button>
            <el-button type="success">下载</el-button>
            <el-button type="success">上传</el-button>
          </el-form-item>
        </el-form>

        <div style="overflow-y: auto; height: 500px; width: 920px">
          <div v-for="APIForm in list">
            <el-form :model="APIForm">
              <el-form-item :label="APIForm.name" style="width: 900px">
                <el-input style="width: 150px" v-model="APIForm.CaseName" placeholder="用例名"></el-input>
                <el-input style="width: 200px" v-model="APIForm.CaseUrl" placeholder="Url"></el-input>
                <el-input style="width: 100px" v-model="APIForm.CaseMethod" placeholder="调用方式"></el-input>
                <br>
                <el-input type="textarea" style="width: 233px; margin-left: 48px;" v-model="APIForm.CaseHeader" placeholder="headers"></el-input>
                <el-input type="textarea" style="width: 233px" v-model="APIForm.CaseParams" placeholder="内容"></el-input>
                <el-button type="danger" v-if="APIForm.name !== '用例1'" @click="remove(APIForm)">删除</el-button>
              </el-form-item>
            </el-form>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "APICase",
        data() {
          return {
            list: [
              {
                CaseName:'',
                CaseUrl:'',
                CaseMethod:'',
                CaseHeader:'',
                CaseParams:'',
                name: '用例1'
              }
            ],
            listLong: 2
          }
        },
      methods:{
          remove(item){
            var index = this.list.indexOf(item);
            if (index !== -1) {
              this.list.splice(index, 1);
              this.listLong--;
            }

            for(let i in this.list){
              this.list[i].name = '用例' + (++i);
            }},

          add(){
            let name = '用例'+ this.listLong++;
            this.list.push(
              {
                CaseName:'',
                CaseUrl:'',
                CaseMethod:'',
                CaseHeader:'',
                CaseParams:'',
                name: name
              }
            )
            console.log(this.list);
            },
          download_be_excel(){},
          upload_to_zentao(){}
      }

    }
</script>

<style scoped>
  .el-input{
    margin-right: 10px;
  }
  .el-textarea{
    margin-top: 10px;
    margin-right: 10px;
    vertical-align: top;
  }
  /*定义滚动条轨道 内阴影+圆角*/
  ::-webkit-scrollbar-track
  {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 10px;
    background-color: #F5F5F5;
  }

  /*定义滑块 内阴影+圆角*/
  ::-webkit-scrollbar-thumb
  {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(196, 196, 196, 0.3);
    background-color: #7d7d7d;
  }

  .el-scrollbar__wrap {
    overflow-x: hidden;
  }
</style>



