<template>
    <div style="height:100%; padding: 30px" >
      <span>功能用例</span>
      <div >
        <el-form>
          <el-form-item>
            <el-button type="primary" @click="add">新增</el-button>
            <el-button type="success">下载</el-button>
            <el-button type="success">上传</el-button>
          </el-form-item>
        </el-form>


      <!--<el-scrollbar class="escl" style="height: 500px;">-->

        <div style="overflow-y: auto; height: 500px; width: 920px">
          <div v-for="FuncForm in list">
            <el-form :model="FuncForm">
              <el-form-item :label="FuncForm.name" style="width: 900px">
                <el-input style="width: 150px" v-model="FuncForm.CaseName" placeholder="用例名"></el-input>
                <el-input style="width: 150px" v-model="FuncForm.CaseExpect" placeholder="预期结果"></el-input>
                <el-input style="width: 150px" v-model="FuncForm.CaseRemark" placeholder="备注"></el-input>
                <br>
                <el-input  type="textarea" style="width: 233px; margin-left: 48px;" v-model="FuncForm.CaseContition" placeholder="前置条件"></el-input>
                <el-input type="textarea" style="width: 233px" v-model="FuncForm.CaseStep" placeholder="具体步骤"></el-input>
                <el-button type="danger" v-if="FuncForm.name !== '用例1'" @click="remove(form)">删除</el-button>
              </el-form-item>
            </el-form>
          </div>
        </div>
      <!--</el-scrollbar>-->
      </div>
    </div>
</template>

<script>
    export default {
        name: "FunctionCase",
        data() {
          return {
            list: [
              {
                CaseName:'',
                CasePath:'',
                CaseStep:'',
                CaseContition:'',
                CaseExpect:'',
                CaseRemark:'',
                name: '用例1'
              }
            ],
            listLong: 2
          };
        },
        methods: {
          remove(item) {
            var index = this.list.indexOf(item);
            if (index !== -1) {
              this.list.splice(index, 1);
              this.listLong--;
            }

            for(let i in this.list){
              this.list[i].name = '用例' + (++i);
            }
          },
        add(){
            let name = '用例'+ this.listLong++;
            this.list.push(
              {
                CaseName:'',
                CasePath:'',
                CaseStep:'',
                CaseContition:'',
                CaseExpect:'',
                name: name
              }
            )
          console.log(this.list);
        },
        download_be_excel(){},
        upload_to_zentao(){}
        }
    }
</script>

<style scoped>
  .el-input{
    margin-right: 10px;
  }
  .el-textarea{
    margin-top: 10px;
    margin-right: 10px;
    vertical-align: top;
  }

  ::-webkit-scrollbar
  {
    width: 4px;
    height: 4px;
    background-color: #F5F5F5;
  }

  /*定义滚动条轨道 内阴影+圆角*/
  ::-webkit-scrollbar-track
  {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 10px;
    background-color: #F5F5F5;
  }

  /*定义滑块 内阴影+圆角*/
  ::-webkit-scrollbar-thumb
  {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(196, 196, 196, 0.3);
    background-color: #7d7d7d;
  }

  .el-scrollbar__wrap {
    overflow-x: hidden;
  }
</style>



