<template>
    <div class="el-head">
      <el-header style="text-align: right; font-size: 12px;">
        <span class="header-title">简单测试平台</span>
        <el-dropdown>
          <i class="el-icon-setting" style="margin-right: 15px"></i>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>修改密码</el-dropdown-item>
            <el-dropdown-item>退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <span>SAM</span>
      </el-header>

      <div class="el-asider">
        <el-container style="border: 1px solid #eee">
          <el-aside width="200px">
            <el-menu>
              <el-submenu index="1">
                <template slot="title"><i class="el-icon-message"></i>用例编写</template>
                <el-menu-item-group>
                  <el-menu-item index="1-1" @click="gotoPage(url.url1)">功能用例</el-menu-item>
                  <el-menu-item index="1-2" @click="gotoPage(url.url2)">接口用例</el-menu-item>
                </el-menu-item-group>
              </el-submenu>
              <el-submenu index="2">
                <template slot="title"><i class="el-icon-menu"></i>测试方式</template>
                <el-menu-item-group>
                  <el-menu-item index="2-1" @click="gotoPage(url.url3)">接口测试</el-menu-item>
                  <el-menu-item index="2-2">压力测试</el-menu-item>
                </el-menu-item-group>
              </el-submenu>
              <el-submenu index="3">
                <template slot="title"><i class="el-icon-setting"></i>文档校验</template>
                <el-menu-item-group>
                  <el-menu-item index="3-1" @click="gotoPage(url.url4)">数据库DDL文件格式校验</el-menu-item>
                </el-menu-item-group>
              </el-submenu>
            </el-menu>
          </el-aside>
        </el-container>
      </div>
    </div>
</template>

<script>
    export default {
        name: "Aside",
      data() {
        return {
          url: {
            url1: '/FunctionCase',
            url2: '/APICase',
            url3: '/APITest',
            url4: '/DDLCheck'
          }
        }
      },
      methods:{
          gotoPage(url){
            this.$router.push(url);
          }
      }
    }
</script>

<style scoped>
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }

  .el-aside {
    color: #333;
  }
  .header-title{
    position: absolute;
    left: 0;
    font-weight: bold;
    font-size: 22px;
    margin: 0 20px;
  }

  .el-asider{
    width: 200px;
    /*height: calc(100% - 60px);  !*CSS计算属性,运算符号两边空格*!*/
    height: calc(100% - 60px);
    background: white;
  }

  .el-head{
    height: 100%;
    weight: 100%
  }
</style>




