<template>
    <div>
      <span>接口测试</span>
      <div>
        <el-form>
          <el-form-item>
            <el-button type="primary">上传</el-button>
            <el-button type="success">跑接口</el-button>
          </el-form-item>
        </el-form>
      </div>

      <div class="requestArea" style="overflow-y: auto; height: 150px; width: 920px">
        <div>
          <el-form :model="APIForm">
            <el-form-item :label="APIForm.name" style="width: 900px">
              <el-input style="width: 200px; margin-left: 8px" v-model="APIForm.CaseUrl" placeholder="Url"></el-input>
              <el-input style="width: 100px" v-model="APIForm.CaseMethod" placeholder="调用方式"></el-input>
              <br>
              <el-input type="textarea" style="width: 233px; margin-left: 48px;" v-model="APIForm.CaseHeader" placeholder="headers"></el-input>
              <el-input type="textarea" style="width: 233px" v-model="APIForm.CaseParams" placeholder="内容"></el-input>
              <el-button type="primary" @click="postUrl(APIForm)">发送</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>

      <div class="responeArea">
        <span>响应报文</span>

        <div style="height: 400px">
          <el-input type="textarea" class="requestRead" style="margin-left: 48px; width: 480px;" :rows="10" v-model="all" ></el-input>
        </div>

      </div>
    </div>
</template>

<script>
    export default {
        name: "APITest",
        data(){
          return {
            APIForm: {
              CaseUrl: '',
              CaseMethod: '',
              CaseHeader: '',
              CaseParams: '',
              name: '调试'
            },
            all:'sadfasdf'
          }
        },
        methods:{
          postUrl(item){
            let requestData =
            this.$axios({
              method: 'post',
              url: 'getRequest/post/',
              data: {
                "params" : item.CaseParams,
                "requestUrl": item.CaseUrl,
                "Headers" : item.CaseHeader
              },
              dataType: 'json'
            }).then(
                (req) => {
                  console.log(req.data.data);
                  this.all = JSON.stringify(req.data.data);
                  console.log(req);
            })
          },
          getUrl(){},
          getReapone(){

          }
        }
    }
</script>

<style scoped>
  .el-input{
    margin-right: 10px;
  }
  .el-textarea{
    margin-top: 10px;
    margin-right: 10px;
    vertical-align: top;
  }

  /*定义滚动条轨道 内阴影+圆角*/
  ::-webkit-scrollbar-track
  {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 10px;
    background-color: #F5F5F5;
  }

  /*定义滑块 内阴影+圆角*/
  ::-webkit-scrollbar-thumb
  {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(196, 196, 196, 0.3);
    background-color: #7d7d7d;
  }

  .el-scrollbar__wrap {
    overflow-x: hidden;
  }
</style>



